.viewPogoda__div {
  padding-left: 10px;
}

.opis {
    font-weight: bold;
}

.viewPogodaMiejscowa > div{
    padding: 20px 0px;
    align-items: center;
    align-content: center;
    background-color: white;
}

.viewPogodaImages{
    width: 40px;
    height: 40px;
    margin: 0;
    padding: 0;
}

.ViewPogodaImagesDiv {
  display: flex;
  border-top: 1px solid black;

}   
.viewPogodaMiejscowa {
    display: grid;
    margin: 0px;
    padding: 0px;
    grid-template-columns: 1fr 60px 40px 60px 40px;
    grid-template-rows: repeat(5,60px);
    border: 1px solid black;
    background-color: rgb(166, 194, 226)
}


@media(min-width: 500px) {
    .viewPogodaMiejscowa {
        display:grid;
        grid-template-columns: 1fr 100px 100px 100px 100px;
        grid-template-rows: repeat(5,60px);
        background-color: rgb(166, 194, 226)
    }
}
